    margin: 0;
    padding: 0;
}

body {
    color: #333;
    font-family: Geneva, Arial, Helvetica, sans-serif;
    font-size: 16px;

    display: flex;
    flex-direction: column;
    height: 100vh;
}

.header {
    padding: 0.5em 1em;
    border-bottom: thin solid silver;
}
.main {
    flex: 1;
    overflow: auto;
    display: grid;
    align-items: center;
    justify-content: center;
}

.error {
    border-radius: 0.2em;
    border: thin solid red;
    border-left-width: thick;
    color: darkred;
    padding: 1em 2em;
    background: #FEE;
}

.progress-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5em;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,WAAW;IACX,iDAAiD;IACjD,eAAe;;IAEf,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,gCAAgC;AACpC;AACA;IACI,OAAO;IACP,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;IACpB,sBAAsB;IACtB,wBAAwB;IACxB,cAAc;IACd,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,UAAU;AACd","sourcesContent":["html, body, div, p, h1, h2, h3, h4, ul, li, span, form, input, select, button {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\nbody {\r\n    color: #333;\r\n    font-family: Geneva, Arial, Helvetica, sans-serif;\r\n    font-size: 16px;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    height: 100vh;\r\n}\r\n\r\n.header {\r\n    padding: 0.5em 1em;\r\n    border-bottom: thin solid silver;\r\n}\r\n.main {\r\n    flex: 1;\r\n    overflow: auto;\r\n    display: grid;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.error {\r\n    border-radius: 0.2em;\r\n    border: thin solid red;\r\n    border-left-width: thick;\r\n    color: darkred;\r\n    padding: 1em 2em;\r\n    background: #FEE;\r\n}\r\n\r\n.progress-box {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    gap: 0.5em;\r\n}"],"sourceRoot":""}]);
